<template>
  <div>
    <Button @click="modal = true" type="primary" style="margin-bottom: 30px;float: left;">新增强关链接</Button>
    <Table class="links-table" stripe :columns="linksHeader" :data="linksList"></Table>
    <div class="modal" v-if="modal">
      <h3>新增/编辑强关链接</h3>
      <Input style="margin: 10px 0;" type="text" v-model="form.jumpLink" placeholder="请输入跳转链接"/>
      <Input type="text" v-model="form.wechatId" placeholder="请输入微信原始ID"/>
      <Button class="btn" type="warning" @click="modalClose">取消</Button>
      <Button class="btn" type="success" @click="modalSubmit">确定</Button>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
export default {
  data() {
    return {
      modal: false,
      index: null,
      isUpdate: false,
      form: {},
      linksList: [],
      linksHeader: [
        {
          title: "id",
          key: "id",
          align: "center"
        },
        {
          key: "jumpLink",
          title: "跳转链接",
          align: "center"
        },
        {
          key: "wechatId",
          title: "微信原始ID",
          align: "center"
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            console.log(params.row)
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    size: "small",
                    type: "primary"
                  },
                  on: {
                    click() {
                      that.isUpdate = that.modal = true;
                      that.index = params.index;
                      that.form = params.row;
                    }
                  }
                },
                "修改"
              ),
              h(
                "Button",
                {
                  props: {
                    size: "small",
                    type: "error"
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: "提示:",
                        content: "确认删除这条内容吗？",
                        onOk: () => {
                          that.$axios
                            .get("/qiangguan/del", {
                              params: { _id: params.row._id }
                            })
                            .then(res => {
                              if (res.data.success) {
                                that.$Message.info(res.data.success);
                                that.linksList.splice(params.index, 1)
                              } else {
                                that.$Message.info(res.data.error);
                              }
                            });
                        }
                      });
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showLinksList()
  },
  methods: {
    showLinksList() {
      this.$axios.get("/qiangguan").then(res => {
        if (res.data.success) {
          this.linksList = res.data.data;
        }
      });
    },
    modalClose() {
      this.form = {};
      this.modal = false;
      this.showLinksList()
    },
    modalSubmit() {
      if (!this.isUpdate) {
        this.$axios.post("/qiangguan/create", this.form).then(res => {
          if (res.data.success) {
            this.$Message.info(res.data.success);
            this.modalClose();
          } else {
            this.$Message.info(res.data.error);
          }
        });
      } else {
        this.$axios.post("/qiangguan/update", this.form).then(res => {
          if (res.data.success) {
            this.$Message.info(res.data.success);
            this.modalClose();
            this.isUpdate = false;
          } else {
            this.$Message.info(res.data.error);
          }
        });
      }
    }
  }
};
</script>

<style scoped>
.links-table {
  clear: both;
}
.modal {
  width: 300px;
  /* height: 180px; */
  padding: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -165px;
  margin-top: -105px;
  background: #eee;
  z-index: 100000;
  color: #999;
  text-align: left;
}
.modal .btn {
  margin-top: 10px;
  margin-right: 20px;
}
</style>
